import React, { PureComponent } from 'react';
import Query from '../Query';
import filter from '../../utils/filter';

class Form extends PureComponent {
    formatDate(date) {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2) month = '0' + month;
        if (day.length < 2) day = '0' + day;

        return [year, month, day].join('-');
    }
    mergeData(defaults, data) {
        let result = {};
        Object.keys(defaults).forEach(e => {
            // console.log(e);
            result[e] = { ...defaults[e] };
            if (typeof (defaults[e].default) === "string" || typeof (defaults[e].default) === "number") {
                result[e].default = data[e];
            }
            else if (defaults[e].default instanceof Array) {
                // result[e].default = data[e];                
            }
            else if (defaults[e].type === Boolean) {
                result[e].default = data[e];
            }

            else if (defaults[e].type === "date") {
                result[e].default = this.formatDate(data[e]);
            }

            else if (defaults[e].default instanceof Object) {
                result[e].default = { ...this.mergeData(defaults[e].default, data[e]) };
            }
            else {
                throw new Error("merge error");
            }
        });
        return result;
    }
    render() {
        const { edit = false, ...restParams } = this.props.match.params;        
        const { query, getQueryVariables = ({ id }) => ({ id }), defaults: prevDefaults, skipOnUpdate } = this.props;
        let defaults = prevDefaults;
        if (edit) {
            defaults = filter(prevDefaults, skipOnUpdate);
            return (
                <Query query={query} variables={getQueryVariables(restParams)} fetchPolicy="cache-and-network">
                    {({ loading, error, data }) => {
                        if (loading) {
                            return (<span>loading...</span>);
                        }
                        else if (error) {
                            return (<span>Error Occured!</span>);
                        }
                        let newDefaults = this.mergeData(defaults, data[Object.keys(data)[0]]);
                        return this.props.children({ update: true, defaults: newDefaults, queryResult: data });
                    }}
                </Query>
            );
        }
        return this.props.children({ update: false, defaults, queryResult: null });
    }
};

export default Form;



