import { gql } from 'apollo-boost';

export const USERS = gql`
    query users($limit: Int, $skip: Int){
        users(limit: $limit, skip: $skip) {
            id,
            username,            
            siteCount,
            totalSitesCost,
            totalReceivedAmount,
            spent,
            balance,
            createdAt,
            updatedAt,
            count,
        }
    }
`;

export const USER = gql`
    query user($id: String!){
        user(id: $id) {
            id,
            username,
            siteCount,
            totalSitesCost,
            totalReceivedAmount,
            spent,
            balance,
            createdAt,
            updatedAt            
        }
    }
`;

export const USER_CREDIT_HISTORY = gql`
    query userCreditHistory($id: String!, $limit: Int, $skip: Int){
        userCreditHistory(id: $id, limit: $limit, skip: $skip) {
            count
            history {
                amount
                createdAt
            }
        }
    }
`;

export const USERS_USERNAME = gql`
    query users($limit: Int, $skip: Int){
        users(limit: $limit, skip: $skip) {
            id
            username
        }
    }
`;

export const SITELIST = gql`
  query sites($limit: Int, $skip: Int) {
    sites(limit: $limit, skip: $skip) {
        id,
        name,
        location,
        entryCount,
        createdAt,
        updatedAt,
        count
        manager {
            id,
            name: username
        }
        managerSpentAmount,
        cost
    }
  }
`;

export const STORE_SITELIST = gql`
  query sites {
  sites {
    id
    name
    location
    entryCount
    createdAt
    updatedAt
    count
    manager {
      id
      name: username
    }
    managerSpentAmount
    cost
  }
}
`;

export const SITE = gql`
  query site($id: String!) {
    site(id: $id) {
        id,
        name,
        location,
        createdAt,
        updatedAt,
        count
        manager {
            id,
            name: username
        }        
    }
  }
`;

const repeater = `{
    quantity,
    cost,
    paid
}`;

export const SITE_ENTRIES = gql`
    query siteEntries($id: String!, $limit: Int!, $skip: Int){
        siteEntries(id: $id, limit: $limit, skip: $skip){
            id,
            name,
            location,
            cost,
            count,
            manager {
                id,
                name: username,                
            },
            managerSpentAmount,
            entries
            {
                id,
                createdAt,
                updatedAt,                
                mistri ${repeater},
                labour ${repeater},
                eit ${repeater},
                morang ${repeater},
                githi ${repeater},
                cement ${repeater},
                saria ${repeater},
                dust ${repeater},
                other ${repeater},
                managerSpentAmount,
                total
            }
        }
    }
`

export const SITEENTRY = gql`
    query siteEntry($id: String!){
        siteEntry(id: $id){
            id,
            createdAt,
            updatedAt,                
            mistri ${repeater},
            labour ${repeater},
            eit ${repeater},
            morang ${repeater},
            githi ${repeater},
            cement ${repeater},
            saria ${repeater},
            dust ${repeater},
            other ${repeater},
            managerSpentAmount,
            total
        }
    }
`



