import React, { PureComponent } from 'react';
import { MAKESITEENTRY, UPDATESITEENTRY } from '../../graphql/mutation/remote';
import InputAdornment from '@material-ui/core/InputAdornment';
import Form from './Form';
import FormGenerator from './FormGenerator';
import { SITEENTRY } from '../../graphql/query/remote';

class CreateSiteEntry extends PureComponent {
    render() {
        const repeater = {
            quantity: {
                type: "number",
                default: 0
            },
            cost: {
                type: "number",
                default: 0,
                startAdornment: <InputAdornment position="start">₹</InputAdornment>
            },
            paid: {
                type: Boolean,
                default: false
            }
        };
        const defaults = {
            mistri: {
                default: repeater
            },
            labour: {
                default: repeater
            },
            eit: {
                default: repeater
            },
            morang: {
                default: repeater
            },
            githi: {
                default: repeater
            },
            cement: {
                default: repeater
            },
            saria: {
                default: repeater
            },
            dust: {
                default: repeater
            },
            other: {
                default: {
                    quantity: {
                        type: "text",
                        default: "N/A",
                    },
                    cost: {
                        type: "number",
                        default: 0
                    },
                    paid: {
                        type: Boolean,
                        default: false
                    }
                }
            },
            createdAt: {
                type: "date",
                default: new Date()

            }
        };
        const { siteId, id } = this.props.match.params;
        return (
            <Form
                {...this.props}
                query={SITEENTRY}
                createMutation={MAKESITEENTRY}
                updateMutation={UPDATESITEENTRY}
                defaults={defaults}>
                {({ update, defaults }) => {
                    return (
                        <FormGenerator
                            {...this.props}
                            defaults={defaults}
                            mutation={update ? UPDATESITEENTRY : MAKESITEENTRY}
                            title={!update ? "Create a new entry" : "Update this entry"}
                            submitText={!update ? "Create Entry" : "Update Entry"}
                            mutateFunc={(fn, data) => { return fn({ variables: { siteId, id, data } }); }}
                        >
                        </FormGenerator>
                    );
                }}
            </Form>
        )
    }
};

export default CreateSiteEntry;



